              class="ml-n3"
              v-show="loading == false"
            >
              <v-timeline-item
                fill-dot
                :small="mobile ? false : true"
                v-for="log in Log"
                :key="log.Jam+log.Job"
                :icon="log.Job.includes('Login') ? 'mdi-login' : log.Job.includes('Logout') ? 'mdi-logout' : log.Job.includes('Mengubah') ? 'mdi-pencil' : log.Job.includes('Menghapus') ? 'mdi-trash-can' : 'mdi-account-plus'"
                :color="log.Job.includes('Login') ? 'green darken-3' : log.Job.includes('Logout') ? 'red darken-3' : log.Job.includes('Mengubah') ? 'blue darken-4' : log.Job.includes('Menghapus') ? 'red darken-4' : 'blue darken-4'"
              >
                <v-card :outlined="$vuetify.theme.dark ? true : false" class="pl-3 pb-2 pt-1 mr-4 rounded-lg ml-n2">
                  <v-row dense class="pt-1">
                    <v-col cols="4" sm="3" md="2" lg="2">
                      <strong>{{log.Jam}}</strong>
                      <div class="text-caption">
                        {{log.Tanggal}}
                      </div>
                    </v-col>
                    <v-col cols="11" sm="8" md="9" lg="9">
                      <strong>{{log.KodeUser}}</strong>
                      <div class="text-caption">
                        {{log.Job}}
                      </div>
                    </v-col>
                  </v-row>
                </v-card>
              </v-timeline-item>
            </v-timeline>
          </v-sheet>
        </v-card>
      </div>

      <v-fab-transition>
        <v-btn
          :color="$vuetify.theme.dark ? 'grey darken-3' : 'grey darken-1'"
          v-scroll="onScroll"
          v-show="fab"
          fab
          small
          dark
          fixed
          bottom
          right
          @click="toTop"
        >
          <v-icon>mdi-chevron-up</v-icon>
        </v-btn>
      </v-fab-transition>
    </v-container>

