<script>
import api from "@/services/http";
  export default {
    data: () => ({
      fab: false,
      mobile:null,
      windowSize: {x: 0, y: 0},
      loading:true,
      DialogUserMenu: false,
      editedIndex: -1,
      token:null,
      MenuTemp:[],
      ParentTemp:[],
      ObjectTemp:[],
      ParentSelect:null,
      JabatanSelect:null,
      resMenu:[],
      defaultItem: {
        KodeMenu: "",
        Nama: "",
        Object: "",
        UserMenu: "",
        Visible: "",
        Icon: ""
        
      },
      editedItem: {
        KodeMenu: "",
        Nama: "",
        Object: "",
        UserMenu: "",
        Visible: "",
        Icon: ""
        
      },
      Log:[],
      Beranda: [
        // { Nama: 'Beranda'},
      ],

      Master: [
        // {
        //   items: [
        //     { Nama: 'Karyawan' },
        //     { Nama: 'UserMenu' }

        //   ],
        //   Nama: 'Master',
        // },
      ],
    }),

    mounted(){
      this.token = localStorage.getItem('token')
      this.getLog()
      this.ee()
    },

    computed: {
      formTitleUserMenu () {
        return this.editedIndex === -1 ? 'Tambah User Menu' : 'Ubah User Menu'
      },
    },

    watch: {
      windowSize(){
        if (this.windowSize.x < 700) {
          this.titleClass = "d-none"
          this.mobile = true
        }else{
          this.titleClass = "mr-4"
          this.mobile = false
        }
      },

      DialogUserMenu(val) {
        val || this.KeluarDialogUserMenu();
        if (this.DialogUserMenu == true) {
          this.ObjectTemp = ['List','Link']
          api.get('menuselected/'+this.JabatanSelect+'?token='+this.token).then(res=>{
            this.MenuTemp = res.data
            var parent = res.data.Menu.filter( function(item){return (item.Parent == null && item.Object != "Link");} );
            this.ParentTemp = parent
          })
        }
      },
      ParentSelect(){
        if (this.ParentSelect == null || this.ParentSelect.length == 0) {
          //do nothing
          this.ObjectTemp = [...new Array]
          this.ObjectTemp = ['List','Link']
        }else if(this.ParentSelect.Object == "List") {
          this.ObjectTemp = ['Page']
        }else{
          //do nothing
        }
      }
    },

    DialogUserMenu (val) {
      val || this.KeluarDialogUserMenu()
    },

    methods: {
      onScroll (e) {
        if (typeof window === 'undefined') return
        const top = window.pageYOffset ||   e.target.scrollTop || 0
        this.fab = top > 20
      },
      toTop () {
        this.$vuetify.goTo(0)
      },
      
      onResize(){
        this.windowSize = { x: window.innerWidth, y: window.innerHeight }
      },

      getLog:function(){
        api.get('log').then(
        res=>{
          this.loading = false
          let a = JSON.stringify(res.data)
          this.Log = [...new Array]
          this.Log = JSON.parse(a)
        //   this.Log = a
        })
        // api.get('/log').then(
		// 		res => {
        //   console.log(res.data)
        //   this.Log = res.data
		// 		},
		// 		err => {
		// 			console.log(err);
		// 		}
		// 	)
      },
      ee(){
        // let url = "http://192.168.1.76:8000/log"
        // fetch(url)
        // .then((resp) => resp.json())
      },

      KeluarDialogUserMenu () {
        this.DialogUserMenu = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.ParentSelect = null
          this.editedIndex = -1
        })
      },

      Reset() {
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
        })
      },
    },
  }
</script>

